.hamStick{
    width: 30px;
    height:2px;
    background: #fff;
}
.ham{
    height: 40px;    
    padding: 5px;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    transition: 0.2s;
}

.active .ham1{
    transform: rotate(45deg) translate(5px, 5px);
}
.active .ham1{
    transform: rotate(45deg) translate(5px, 5px);
}
.active .ham2{
    filter: opacity(0%);
}
.active .ham3{
    transform: rotate(-45deg)  translate(5px, -5px);
}
.ham1{
    
    transition: 0.2s;
}
.ham2{
    transition: 0.2s;
}
.ham3{
    
    transition: 0.2s;
}
.active{
    border: 1px solid grey;
    border-radius: 50%;
}

.icon{
    
    background-size: cover;
}
.resNav{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 70vh;
    right: 10%;
}

.resNav .resTabs{
    padding: 8px 19px;
    border-radius: 10px;
    backdrop-filter: 12px;
    background-color: rgba(137, 43, 226, 0.466);
    box-shadow: 0 0 25px 9px rgba(137, 43, 226, 0.466);
    cursor: pointer;
}
